							}
						},
					}]
					
				}
                // 使用刚指定的配置项和数据显示图表。
                zhuyanChart1.setOption(option);
                  //根据窗口的大小变动图表
                window.onresize = function() {
                    zhuyanChart1.resize();
                };
            }
        });
      })
    },

//统计接口
    chartDialog2() {
