//统计接口1
    remendianyingChat1() {
      this.$nextTick(()=>{

        var remendianyingChart1 = echarts.init(document.getElementById("remendianyingChart1"),'caravan');
        this.$http({
            url: "remendianying/group/zhuyan",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.funnelNum){
						break;
					}
                    xAxis.push(res[i].zhuyan);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].zhuyan
                    })
                }
                var option = {};
				let titleObj = this.funnel.title
				titleObj.text = '主演统计'
				
				let legendObj = {
					data: xAxis,
				}
				legendObj = Object.assign(legendObj , this.funnel.legend)
				let seriesObj = {
					name: '主演统计',
					data: pArray,
					type: 'funnel',
					left: '10%',
					top: 60,
					bottom: 60,
					width: '80%',
					minSize: '0%',
					maxSize: '100%',
				}
				seriesObj = Object.assign(seriesObj , this.funnel.series)
				const gridObj = this.funnel.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: "{b} : {c}"
				}
				tooltipObj = Object.assign(tooltipObj , this.funnel.tooltip?this.funnel.tooltip:{})
				option = {
					backgroundColor: this.funnel.backgroundColor,
					color: this.funnel.color,
				    title: titleObj,
				    legend: legendObj,
				    tooltip: tooltipObj,
					grid: gridObj,
				    series: seriesObj,
				}
                // 使用刚指定的配置项和数据显示图表。
                remendianyingChart1.setOption(option);
				

                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    remendianyingChart1.resize();
                },false);
            }
        });
      })
    },

