				xAxisObj.data = xAxis1
				let yAxisObj = this.line5?this.line5.yAxis:this.line.yAxis
				yAxisObj.type = 'value'
				let seriesObj = [
                    {
                        data: yAxis1,
                        type: 'line',
                        name: '评分',
                    },
                    {
                        data: yAxis2,
                        type: 'line',
                        name: '评价数',
                    },
                    {
                        data: yAxis3,
                        type: 'line',
                        name: '五星数',
                    },
                ]
                for(let i=0;i<seriesObj.length;i++){
				      seriesObj[i] = Object.assign(seriesObj[i] , this.line5?this.line5.series:this.line.series)
                }
				const gridObj = this.line5.grid
				let tooltipObj = {
					trigger: 'axis',
				}
				tooltipObj = Object.assign(tooltipObj , this.line5.tooltip?this.line5.tooltip:{})
                option = {
					backgroundColor: this.line5?this.line5.backgroundColor:this.line.backgroundColor,
					color: this.line5?this.line5.color:this.line.color,
					title: titleObj,
					legend: legendObj,
                    tooltip: tooltipObj,
					xAxis: xAxisObj,
					yAxis: yAxisObj,
					grid: gridObj,
					series: seriesObj
                };
                // 使用刚指定的配置项和数据显示图表。
                dianyingxinxiChart4.setOption(option);
				

                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    dianyingxinxiChart4.resize();
                },false);
            }
        });
      })
    },

//统计接口2
    dianyingxinxiChat5() {
      this.$nextTick(()=>{

        this.$http({
            url: "group/dianyingxinxi/title",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].title);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].title
                    })
                }
                var option = {};
                this.wordclouds(pArray,'dianyingxinxiChart5');
				

            }
        });
      })
    },






































  }
};
